body,
html {
  height: 100%;
  font-family: 'Open Sans', sans-serif; }

* {
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif; }

#root {
  height: 100%; }

.scrumma-group {
  display: flex;
  min-height: 100%;
  overflow-y: auto; }
  .scrumma-group > div {
    flex-basis: 33.3333%;
    min-height: 100%;
    padding: 20px; }

.history {
  color: #fff;
  background-image: -webkit-linear-gradient(135deg, #ffc796 0%, #ff6b95 100%);
  background-image: linear-gradient(-45deg, #ffc796 0%, #ff6b95 100%); }

.votes {
  background-color: #fafafa; }

.options {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  .options > button {
    flex-basis: 30%;
    padding: 0;
    border: none;
    border-radius: 4px;
    background-image: -webkit-linear-gradient(135deg, #ffc796 0%, #ff6b95 100%);
    background-image: linear-gradient(-45deg, #ffc796 0%, #ff6b95 100%);
    margin-bottom: 15px;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out; }
    .options > button > div {
      width: 100%;
      height: 0;
      padding-bottom: 100%;
      display: block;
      position: relative; }
      .options > button > div > span {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-size: 32px; }
    .options > button:hover {
      -webkit-transform: scale(1.05);
              transform: scale(1.05);
      -webkit-transition: all 0.2s ease-in-out;
      transition: all 0.2s ease-in-out; }

